import Admin from '@/views/Admin'
import skillSelling from './admin/skillSelling'
import financialCenter from './admin/financialCenter'
import operateCenter from './admin/operateCenter'
import map from './admin/map'
import advertisingManagement from './admin/advertisingManagement'
export default {
    path: '/',
    name: 'Admin',
    component: Admin,
    children: [
        // 首页
        {
            path: '/',
            name: 'home',
            component: () => import('../views/Admin/home')
        },
        // 技能出售
        ...skillSelling,
        // 财务中心 
        ...financialCenter,
        //运营中心
        ...operateCenter,
        //地图模块
        ...map,
        //广告管理
        ...advertisingManagement,
        //商家管理
        {
            path: '/businessManagement',
            name: 'BusinessManagement',
            component: () => import('../views/Admin/BusinessManagement')
        },
        //404
        {
            path: '*',
            component: () => import('../views/Admin/404')
        }
    ]
}
