<template>
  <div>
    <el-dialog :title="dialogVisibleMessage.title" :visible.sync="dialogVisible" width="30%">
      <el-input type="textarea" :placeholder="dialogVisibleMessage.placeholder"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="determine">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template> 

<script>
export default {
  props: ["dialogVisibleMessage", "dialogVisible"],
  methods: {
    determine() {
      this.$parent.dialogDetermine();
    },
    cancel() {
      this.$parent.dialogCancel();
    }
  }
};
</script>