export function handleParent(path) {
    if (
        path == "/serviceadmin" ||
        path == "/residentAudit" ||
        path == "/serviceSupervise" ||
        path == "/serviceRecord" ||
        path == "/serviceadminedit"

    ) {
        return "技能出售";
    }
    if (
        path == "/withdrawal" ||
        path == "/recharge" ||
        path == "/giftGivingRecord" ||
        path == "/inviteFriends"
    ) {
        return "财务中心";
    }
    if (
        path == "/officialAccountManagement" ||
        path == "/userManagement" ||
        path == "/reportAudit" ||
        path == "/feedback" ||
        path == "/giftConfiguration" ||
        path == "/gift" ||
        path == "/handle"
    ) {
        return "运营中心";
    }
    if (
        path == "/lbsMap" ||
        path == "/userActivityList" ||
        path == "/coupon"
    ) {
        return "地图模块";
    }
    if (path == "/bannerList" || path == "/startupList") {
        return "广告管理";
    }
    if (path == "/businessManagement") {
        return "商家管理平台";
    }
    if (path == "/") {
        return "首页";
    }
}

export function handleRouteName(path) {
    switch (path) {
        case "/":
            return false;
        case "/serviceadmin":
            return "服务方管理";
        case "/serviceadminedit":
            return "编辑个人资料";
        case "/residentAudit":
            return "入住审核";
        case "/serviceSupervise":
            return "服务方监管";
        case "/withdrawal":
            return "提现订单";
        case "/recharge":
            return "充值订单";
        case "/giftGivingRecord":
            return "送礼记录";
        case "/inviteFriends":
            return "邀请好友提现";
        case "/officialAccountManagement":
            return "公众号管理";
        case "/userManagement":
            return "用户管理";
        case "/reportAudit":
            return "举报审核";
        case "/feedback":
            return "意见反馈";
        case "/handle":
            return "处理举报";
        case "/giftConfiguration":
            return "礼物配置";
        case "/gift":
            return "添加礼物";
        case "/lbsMap":
            return "LBS地图";
        case "/coupon":
            return "优惠券管理";
        case "/userActivityList":
            return "用户活动列表";
        case "/bannerList":
            return "轮播图列表";
        case "/startupList":
            return "启动列表";
        case '/serviceRecord':
            return "服务记录";
        default:
            return false;
    }
}